@font-face {
  font-family: "Open Sans";
  src: url("../../fonts/opensans.woff") format("woff"), url("../../fonts/opensans.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../../fonts/opensansbold.woff") format("woff"), url("../../fonts/opensansbold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
body {
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", "Arial", sans-serif;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.advantages {
  padding-top: 30px;
  padding-bottom: 29px;
  padding-right: 30px;
}
.advantages__title {
  margin-top: 0;
  margin-bottom: 28px;
}
.advantages__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.advantages__item {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding-bottom: 28px;
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
  color: #000000;
}
.advantages__icon {
  margin-bottom: 18px;
}
.advantages__icon--flower {
  margin-left: 7px;
}
.advantages__icon--money {
  margin-bottom: 27px;
}
.advantages__icon--heart {
  margin-left: 6px;
  margin-bottom: 22px;
}
.advantages__icon--ball {
  margin-left: 3px;
  margin-bottom: 18px;
}
.advantages__icon--rocket {
  margin-left: -5px;
  margin-bottom: 28px;
}
.advantages__icon--gift {
  margin-left: 3px;
  margin-bottom: 21px;
}
.advantages__text--money {
  margin-bottom: 6px;
}
.advantages__text--heart {
  margin-bottom: 3px;
}
.advantages__text--ball {
  margin-bottom: 3px;
}
.advantages__text--rocket {
  margin-bottom: 3px;
}
@media (max-width: 767px) {
  .advantages {
    padding-left: 30px;
  }
  .advantages__text--flower {
    width: 180px;
  }
}
@media (min-width: 768px) {
  .advantages {
    padding-top: 55px;
    padding-bottom: 78px;
  }
  .advantages__title {
    margin-bottom: 40px;
  }
  .advantages__list {
    display: grid;
    grid-template-columns: minmax(260px, 265px) minmax(320px, 325px);
    grid-template-rows: auto;
    column-gap: 74px;
    row-gap: 55px;
  }
  .advantages__item {
    padding-bottom: 0;
    font-size: 20px;
    line-height: 22px;
  }
  .advantages__item:nth-child(1),
  .advantages__item:nth-child(2),
  .advantages__item:nth-child(3) {
    grid-column: 1/2;
  }
  .advantages__item:nth-child(1),
  .advantages__item:nth-child(4) {
    grid-row: 1/2;
  }
  .advantages__item:nth-child(4),
  .advantages__item:nth-child(5),
  .advantages__item:nth-child(6) {
    grid-column: 2/3;
  }
  .advantages__item:nth-child(2),
  .advantages__item:nth-child(5) {
    grid-row: 2/3;
  }
  .advantages__icon {
    margin-bottom: 18px;
  }
  .advantages__icon--money {
    margin-bottom: 25px;
  }
  .advantages__icon--heart {
    margin-left: 8px;
    margin-top: 4px;
    margin-bottom: 22px;
  }
  .advantages__icon--ball {
    margin-left: 4px;
    margin-top: 6px;
  }
  .advantages__icon--rocket {
    margin-bottom: 30px;
  }
  .advantages__icon--gift {
    margin-bottom: 20px;
  }
  .advantages__text--money {
    margin-bottom: 0;
  }
}
@media (min-width: 1150px) {
  .advantages {
    padding-top: 70px;
    padding-bottom: 86px;
  }
  .advantages__list {
    column-gap: 85px;
    row-gap: 53px;
  }
  .advantages__icon--ball {
    margin-top: 4px;
  }
  .advantages__icon--gift {
    margin-bottom: 24px;
  }
  .advantages__icon--heart {
    margin-top: 6px;
    margin-bottom: 23px;
  }
}
